@import "https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap";
:root {
  --color-main: #000;
  --color-second: #fff;
  --color-sat: #f6f6f6;
  --color-enhanced: #783df6;
  --color-enhanced-2: #f9dde7;
  --font-main: "Open Sans", sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 16px;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizelegibility;
  font-size: 1rem;
  line-height: 1.25;
}

a {
  color: inherit;
}

img, video {
  max-inline-size: 100%;
  block-size: auto;
  vertical-align: middle;
}

html {
  scroll-behavior: smooth;
  font-family: var(--font-main);
}

body {
  width: 390px;
  margin: auto;
}

header {
  background-color: var(--color-main);
  color: var(--color-second);
  flex-direction: column;
  align-items: center;
  display: flex;
  position: sticky;
  top: 0;
}

header img {
  margin: 10px auto;
}

header nav {
  background-color: var(--color-second);
  color: var(--color-main);
  width: 100%;
}

header nav ul {
  justify-content: space-between;
  padding: 15px 60px;
  list-style-type: none;
  display: flex;
}

header nav ul li {
  margin-right: 22px;
}

header nav ul li:last-of-type {
  margin-right: 0;
}

.nav-item a {
  font-weight: bold;
  text-decoration: none;
}

.line-up, .films {
  padding: 45px 15px 40px;
}

.line-up h2, .films h2 {
  font-size: 32px;
}

.line-up h2 {
  margin-bottom: 30px;
  line-height: 38px;
}

.article-line-up {
  background-color: var(--color-sat);
  border-bottom: solid 5px var(--color-enhanced);
  cursor: pointer;
  margin: 0 0 38px;
}

.article-line-up p {
  line-height: 28px;
}

.data-article {
  padding: 15px;
}

.data-article h3 {
  margin-bottom: 15px;
  font-size: 20px;
  font-weight: 600;
}

.data-article p {
  font-size: 20px;
}

.article-line-up:last-of-type {
  margin: 0;
}

.member-hiring {
  background-color: var(--color-main);
  color: var(--color-second);
}

.member-hiring-card {
  padding: 15px;
}

.member-hiring-card .member-hire-button {
  background-color: var(--color-second);
  color: var(--color-main);
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  margin: 30px 0 0;
  padding: 15px 30px;
  font-size: 20px;
  font-weight: 600;
  text-decoration: none;
  transition: color .3s, background-color .3s;
  display: inline-block;
}

.member-hire-button:hover, .member-hire-button:active, .member-hire-button:focus {
  background-color: var(--color-enhanced);
  color: var(--color-second);
}

.member-hiring-card h3 {
  margin: 0 0 15px;
  font-size: 24px;
  font-weight: 600;
  line-height: 28px;
}

.member-hiring-card p {
  margin: 0;
  font-size: 20px;
  line-height: 28px;
}

.films h2 {
  margin: 0 0 27px;
  line-height: 38px;
}

.films .container {
  flex-wrap: wrap;
  place-content: space-between;
  align-items: center;
  display: flex;
}

.article-films {
  background-color: var(--color-sat);
  width: 175px;
  cursor: pointer;
  margin-bottom: 10px;
}

.article-films img {
  margin-bottom: 15px;
}

.article-films h3, .article-films p {
  margin: 0 15px;
}

.article-films h3 {
  margin-bottom: 15px;
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
}

.article-films p {
  margin-bottom: 15px;
  font-size: 16px;
  line-height: 22px;
}

.films {
  padding: 45px 15px;
}

.films a {
  background-color: var(--color-main);
  color: var(--color-second);
  margin: auto;
  padding: 15px 30px;
  font-size: 20px;
  font-weight: 600;
  text-decoration: none;
  display: inline-block;
}

footer {
  background-color: var(--color-enhanced-2);
  justify-content: space-between;
  padding: 20px 15px;
  display: flex;
}

.link-ls {
  border-top: solid 3px var(--color-main);
  margin: 10px 10px 0 0;
}

.link-ls:last-of-type {
  margin-right: 0;
}

.link-ls ul {
  list-style-type: none;
}

.link-ls h3 {
  margin: 10px 0;
  font-size: 16px;
  font-weight: 600;
}

.link-ls li {
  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;
  margin-bottom: 10px;
  font-size: 16px;
}

.link-ls li:last-of-type {
  margin-bottom: 0;
}

.hidden-tmp, .hidden-tmp-2 {
  display: none;
}

.show {
  display: block;
}

.show-all {
  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;
}

